import * as MemberApi from '@/api/member/member.js';
export default{
	data(){
		return{
			
		}
	},
	props:{
		shopId:{
			type:Number,
		}
	},
	methods:{
		handleConfirm(e){
			let _this = this;
			let shop_notice = e.detail.value;
			MemberApi.setShopNotice({
				shop_id:_this.shopId,
				shop_notice:shop_notice,
			})
			.then(res => {
				if(res.code > 0){
					_this.$c.msg("店铺公告设置完成")
				}
			})
			
		}
	}
}
